/* width */
::-webkit-scrollbar {
  width: 17px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
 background: #d9a7c7;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fffcdc, #d9a7c7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}
 
/* Handle */
::-webkit-scrollbar-thumb {
border-radius:10px;
  background: #cdcf32;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #26d8df, #1B3332); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}



/* css for dark mood start */
body.light-mode {
    background-color: #fff;
    color: black;
    transition: background-color 0.3s ease;
  }
  
  body.dark-mode{
    background-color: #10171d;
    color: white;
  }
.toggle-control {
    position: relative;
    padding: 0 4px;
    display: flex;
    align-items: center;
  }
input[type='checkbox'].dmcheck {
    width: 28px;
    transform: rotateX(80deg);
    height: 60px;
    background: rgb(17, 156, 121);
    position: relative;
    border-radius: 5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    vertical-align: 2px;
    outline: none;
  }
  .dmcheck:checked + label {
      left: 23px;
    }
  
    .dmcheck  + label {
      display: inline-block;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      transition: all 0.3s ease;
      cursor: pointer;
      position: absolute;
      left: 2px;
      background: #fff;
      opacity: 0.9;
      background-color: #f6f6f6;
    }
