.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 65px;
  background-color: #1b3332;
  text-transform: capitalize;
}
.articles {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 20px;
}
.articles a {
  color: white;
  font-size: 17px;
  border: 2px solid white;
  padding: 4px 8px 4px 8px;
  margin: 0 10px 0 10px;
  transition: font-size 0.1s linear;
}
.coronavirus{
  color: white;
  font-size: 17px;
  border: 2px solid white;
  padding: 4px 8px 4px 8px;
  margin: 0 10px 0 10px;
  transition: font-size 0.1s linear;
}
 .articles a:hover {
  background-color: #19615e;
  transform: scale(1.1);
  border-radius: 2px;
}
.header-span {
  color: white;
  font-size: 25px;
  padding: 4px 8px 4px 8px;
  margin: 3px 20px 0 7px;
  cursor: pointer;
}
.coronavirus {
  background-color: rgb(243, 6, 6);
  transition: all 1s linear;
  width: 123px;
  border-radius: 2px;
  margin-left: 20px;
}
.coronavirus:hover {
  transform: scale(1);
  background-color: rgb(243, 6, 6);
}
.corona {
  background-color: black;
  padding: 9px 10px 5px 5px;
  font-family: "Newsreader", serif;
  position: relative;
  top: -1px;
